import React from 'react';
import Layout from '../components/scheme/Layout';
import { Container } from '../components/scheme/Layout/styled';
import { FormLogin, LoginContainer } from '../styled/LoginStyled';
import Clay from 'images/background/clay.jpg';
import { Button } from '../components/atoms/Button';

const Login = () => {
  return <Layout>
    <Container>
      <LoginContainer>
        <div className='content'>
          <h3>Inicio de sesión</h3>
          <FormLogin>
            <div className='input-group'>
              <label>Numero de dni</label> 
              <input placeholder='Ingrese su DNI'/>
            </div>
            <div className='input-group'>
              <label>Fecha de emision</label> 
              <input type="date" placeholder='Ingrese su Fecha de Emisión'/>
            </div>
            <div className='input-group row'>
              <input type="checkbox"/>
               <label>Recordarme</label>
            </div>
            <div className='input-group row'>
              <input type="checkbox"/>
               <label>Acepto los terminos y condiciones</label>
            </div>
            <div className='buttons'>
              <Button type='submit' primary className='shadow'>
                Ingresar
              </Button>
              <Button link type="button">
                Solicitar primer ingreso
              </Button>
            </div>
          </FormLogin>
        </div>
        <div className='image'>
          <div className='image-parser'></div>
          <img src={Clay} alt='...'/>
        </div>
      </LoginContainer>      
    </Container>
  </Layout>;
};

export default Login;
