import styled from 'styled-components'
export const LoginContainer = styled.div`
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    .content { 
        padding: .5em 3.5em;
        width: 40%;
        position: relative;
        align-self: center;
        /* background-color: red; */
    }
    .image-parser {
        height: calc(100vh - 75px);
        width: 100%;
        background: rgb(195,190,196);
        background: linear-gradient(90deg, rgba(255,255,255,1) 0%, rgba(255,255,255,.8) 10%, rgba(183,195,171,0) 100%);
        opacity: .5;
        position: absolute;
        top: 0;
        right: 0;
    }

    .image {
        width: 60%;
        position: relative;
        overflow: hidden;
    }
    
    .image > img {
        width: 100%;
        border-top-left-radius: .25em;
        border-bottom-left-radius: .25em;
        height: calc(100vh - 75px);
        object-fit: cover;
    }
`

export const FormLogin = styled.form`
    display: flex;
    flex-direction: column;
    gap: 2em;
    padding: 1em 0;

    .input-group label{
        color: #3f3f3f;
        font-family: ${props => props.theme.fonts.fontSecondaryFamily};
        font-size: 11pt;
    }

    input {
        width: 100%;
        padding: 1.2em 1.8em;
        border: solid 1px #e3e3e3;
        border-radius: .25em;
        box-shadow: 2px 5px 5px 0px rgba(0,0,0,0.1);
        -webkit-box-shadow: 2px 5px 5px 0px rgba(0,0,0,0.1);
        -moz-box-shadow: 2px 5px 5px 0px rgba(0,0,0,0.1);
    }
    input[type='checkbox'] {
        width: auto;
    }

    input:active, input:focus {
        outline: none;
    }

    input::placeholder{
        font-family: ${props => props.theme.fonts.fontSecondaryFamily};
    }

    .input-group {
        display: flex;
        flex-direction: column;
        gap: 10px;
    }

    button {
        width: 100%;
        padding: 1.2em 1.8em;
        border-radius: .25em;
        font-family: ${props => props.theme.fonts.fontSecondaryFamily};
        
    }

    .shadow {
        border: solid 1px #e3e3e3;
        box-shadow: 2px 5px 5px 0px rgba(0,0,0,0.1);
        -webkit-box-shadow: 2px 5px 5px 0px rgba(0,0,0,0.1);
        -moz-box-shadow: 2px 5px 5px 0px rgba(0,0,0,0.1);
    }
    .row {
        flex-direction: row;
    }
    

`